.gpt3__navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 2rem 6rem;
    height: 105px;
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 5;
    transition: .4s;
}

.gpt3__navbar.active {
    /* ff 3.6+ */
    background: -moz-radial-gradient(ellipse at 94% 75%, rgba(0, 211, 31, 1) 0%, rgba(75, 237, 183, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background: -webkit-radial-gradient(ellipse at 94% 75%, rgba(0, 211, 31, 1) 0%, rgba(75, 237, 183, 1) 100%);

    /* opera 11.10+ */
    background: -o-radial-gradient(ellipse at 94% 75%, rgba(0, 211, 31, 1) 0%, rgba(75, 237, 183, 1) 100%);

    /* ie 10+ */
    background: -ms-radial-gradient(ellipse at 94% 75%, rgba(0, 211, 31, 1) 0%, rgba(75, 237, 183, 1) 100%);

    /* global 92%+ browsers support */
    background: radial-gradient(ellipse at 94% 75%, rgba(0, 211, 31, 1) 0%, rgba(75, 237, 183, 1) 100%);

    box-shadow: 0 4px 10px #4dff4d;
}

.gpt3__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt3__navbar-links_logo {
    margin-right: 2rem;
}

.gpt3__navbar-links_logo img {
    width: 62.56px;
    height: 16.02px;
}

.gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
    transition: .4s all ease;
}

.gpt3__navbar-links_container p:hover {
    color: var(--color-links);
}

.gpt3__navbar-sign p:hover {
    color: #fff;
    background: #ff4820;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #ff4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
    transition: .4s all ease;
}

.gpt3__navbar-sign p {
    padding: 0.5rem 1rem;
    border-radius: 5px;
    transition: .4s all ease;
}

.gpt3__navbar-sign button:hover {
    background-color: transparent;
    outline: 2px solid #ff4820;
    padding: 0.5rem 1rem;
}

.gpt3__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.gpt3__navbar-menu svg {
    cursor: pointer;
}

.gpt3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.gpt3__navbar-menu_container p {
    margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container {
        display: none;
    }

    .gpt3__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .gpt3__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__navbar {
        padding: 2rem;
    }

    .gpt3__navbar-sign {
        display: none;
    }

    .gpt3__navbar-menu_container {
        top: 20px;
    }

    .gpt3__navbar-menu_container-links-sign {
        display: block;
    }
}