.gpt3__header{
    display: flex;
    flex-direction: row;
}

.gpt3__header-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.gpt3__header-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.gpt3__header-content p{
    font-family: var(--font-family);
    font-weight: normal;
    font-style: normal;
    font-size: 20px;
    color:var(--color-text);
    margin-top: 1.5rem;
    line-height: 27px;
}

.gpt3__header-content__input{
    width:100%;
    margin: 2rem 0 1rem;
    display: flex;
    flex-direction: row;
}

.gpt3__header-content__input input{
    background: var(--color-footer);
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    font-family: var(--font-family);
    flex: 2;
    width:100%;
    min-height: 50px;
    font-size: 20px;
    line-height: 27px;
    border:2px solid var(--color-footer);
    padding:0 1rem;
    outline:none;
    color:#fff;
}

.gpt3__header-content__input button{
    background: #e62a00;
    font-family: var(--font-family);
    font-size: 20px;
    font-weight: bold;
    line-height: 27px;
    flex:0.6;
    width:100%;
    min-height: 50px;
    color:#fff;
    cursor: pointer;
    outline: none;
    padding:0 1rem;
    border:none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    transition: .4s all ease;
}

.gpt3__header-content__input button:hover{
    background: transparent;
    outline: 2px solid #e62a00;
}

.gpt3__header-content__people{
    width:100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
}

.gpt3__header-content__people img{
    width:181.79px;
    height:38px;
}

.gpt3__header-content__people p{
    font-family: var(--font-family);
    font-weight: 500;
    margin:0 0 0 1rem;
    font-size: 12px;
    line-height: 38px;
    color:#fff;
    text-align: center;
}

.gpt3__header-image{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

}

.gpt3__header-image img{
    width:100%;
    height:100%;
}

@media screen and (max-width:1050px){
    .gpt3__header{
        flex-direction: column;
    }

    .gpt3__header-content{
        margin:0 0 3rem;
    }
}

@media screen and (max-width:650px){
    .gpt3__header h1{
        font-size: 48px;
        line-height: 60px;
    }

    .gpt3__header p{
        font-size: 16px;
        line-height: 24px;
    }

    .gpt3__header-content__people{
        flex-direction: column;
    }

    .gpt3__header-content__people p{
        margin:0;
    }

    .gpt3__header-content__input input,
    .gpt3__header-content__input button{
        line-height: 16px;
        font-size: 12px;
    }
}

@media screen and (max-width: 550px){
    .gpt3__header-content{
        margin-top: 1rem;
    }
}

@media screen and (max-width:490px){
    .gpt3__header h1{
        font-size: 36px;
        line-height: 48px;
    }

    .gpt3__header p{
        font-size: 14px;
        line-height: 24px;
    }   
}