.gpt3__whatgpt3{
    display: flex;
    flex-direction: column;

    padding: 2rem;
    background: var(--color-footer);

    /* ff 3.6+ */
    background:-moz-linear-gradient(144deg, rgba(77, 0, 57, 1) 0%, rgba(102, 0, 77, 1) 87%, rgba(230, 0, 172, 1) 100%); 

    /* safari 5.1+,chrome 10+ */
    background:-webkit-linear-gradient(144deg, rgba(77, 0, 57, 1) 0%, rgba(102, 0, 77, 1) 87%, rgba(230, 0, 172, 1) 100%);

    /* opera 11.10+ */
    background:-o-linear-gradient(144deg, rgba(77, 0, 57, 1) 0%, rgba(102, 0, 77, 1) 87%, rgba(230, 0, 172, 1) 100%);

    /* ie 6-9 */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#E600AC', endColorstr='#4D0039', GradientType=1 );

    /* ie 10+ */
    background:-ms-linear-gradient(144deg, rgba(77, 0, 57, 1) 0%, rgba(102, 0, 77, 1) 87%, rgba(230, 0, 172, 1) 100%);

    /* global 94%+ browsers support */
    background:linear-gradient(144deg, rgba(77, 0, 57, 1) 0%, rgba(102, 0, 77, 1) 87%, rgba(230, 0, 172, 1) 100%);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));   
}

.gpt3__whatgpt3-feature{
    display: flex;
}

.gpt__whatgpt3-feature .gpt3__features-containers__feature{
    margin: 0;
}

.gpt__whatgpt3-feature .gpt3__features-containers__feature-text{
    max-width: 700px;
}

.gpt3__whatgpt3-heading{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin:4rem 2rem;
}

.gpt3__whatgpt3-heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    max-width: 510px;
    line-height: 45px;
}

.gpt3__whatgpt3-heading p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color:var(--color-subtext);
    cursor: pointer;
    transition: transform .4s ease-in-out;
}

.gpt3__whatgpt3-heading p:after{
    display:block;
    content: '';
    border-bottom: 2px dashed var(--color-subtext);  
    transform: scaleX(0);  
    transition: transform .3s ease-out;
    transform-origin: 0% 50%;
}

.gpt3__whatgpt3-heading p:hover::after{
    border-bottom: 2px dashed var(--color-subtext);
    transform: scaleX(1);
}

.gpt3__whatgpt3-container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top:2rem;
}

/* Customizing feature component as per needs */
.gpt3__whatgpt3-container .gpt3__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

/* Customizing feature component as per needs */
.gpt3__whatgpt3-container .gpt3__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .gpt3__whatgpt3-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3__whatgpt3-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatgpt3-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-container .gpt3__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}